:root {
  /* --menu-color: rgb(43, 48, 56); */
  --menu-color: rgb(0, 0, 0);
}

.App {
  padding: 10px;
  /* background-color: var(--menu-color); */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.transform-selection {
  margin-bottom: 20px;
  position: relative;
}

.transform-selection select {
  background-color: #ffffff;
  border: 2px solid var(--menu-color);
  border-radius: 4px;
  padding: 5px 10px 5px 10px;
  color: var(--menu-color);
  font-size: 16px;
  cursor: pointer;
  outline: none;
  padding-right: 30px;
}

.drawing-board-container,
.fourier-transform-container {
  margin: 0px;
  background-color: #ffffff;
  padding: 0 10px;
}

canvas {
  border: 1px solid #000000;
  background-color: #ffffff;
}

.drawing-board-container canvas {
  cursor: crosshair;
}
